.Photo {
  background-image: url(../../../assets/images/about/tim-kelly-b&w.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 80vmin;
  width: 100%;
  margin: 0 10vmin;
  box-sizing: border-box;
}

@media screen and (max-width: 767px) {
  .Photo {
    background-image: url(../../../assets/images/about/tim-kelly-b&w-mobile.png);
    background-position: center center;
    height: 140vmin;
    margin: 0;
  }
}
